import React from 'react';
import './li.css';


interface Ipops {
title:string,
time:string,
id:number,
path:string
}

export default class Li extends React.Component<Ipops>{

    constructor(props:Ipops) {
        super(props);

    }


    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
       const {title,time,path} = this.props;
        return <div  className='list_news'>
            <a href={path} className='box' target={path}>
                <div className='top_name'>
                    {title}
                </div>
               <div className='bot_name'>
                   <span className='time'>{time}</span>
                   <span className='btn'>点击查看</span>
               </div>
            </a>
        </div>
    }
}
